import LoginLayout from "../layout/LoginLayout"
import { types } from "../types/types";

import { Link, useNavigate } from "react-router-dom"
import {Container, Row, Col, Card, Button, Form} from 'react-bootstrap'

import { 
    IoFingerPrintOutline, 
    IoEnterOutline,
    IoPersonCircleOutline,
    IoLockClosedOutline,
    IoHelpBuoyOutline
} from "react-icons/io5";

import { useContext } from "react";
import { AuthContext } from '../auth/authContext';

const Login = () => {

    const navigate = useNavigate();
    const { dispatch } = useContext(AuthContext);
    
    const logOn = () => {
    
        const action = {
            type: types.login,
            payload: {
              name: 'JJ',
              lastname: 'Lopez'
            }
          }

        dispatch(action);
    
        navigate('/', {
            replace: true
        });

        console.log('logueado');
    }

    return(
        <>
            <LoginLayout>
                <Container>
                    <Row className="justify-content-center text-md-center mt-4">
                        <Col xs={10} sm={6}>
                            <img src="LegisApp.svg" alt="LegisApp" className="img-fluid" />
                        </Col>
                    </Row>
                    <Row className="justify-content-center m-4">
                        <Col xs={12} sm={8} md={6}>
                            <Card className="mb-4 shadow-sm border-gray animate__animated animate__fadeIn">
                                <Card.Body>
                                    <Card.Title className="fw-light">
                                        <IoFingerPrintOutline /> Acceso Unificado
                                    </Card.Title>
                                    <Form.Group className="mb-3" controlId="formBasicEmail">
                                        <Form.Label className="small fw-light">
                                            <IoPersonCircleOutline /> Usuario
                                        </Form.Label>
                                        <Form.Control type="text" size="lg" placeholder="" />
                                    </Form.Group>
                                    <Form.Group className="mb-3" controlId="formBasicPassword">
                                        <Form.Label className="small fw-light">
                                            <IoLockClosedOutline /> Constraseña
                                        </Form.Label>
                                        <Form.Control type="password" size="lg" placeholder="" />
                                    </Form.Group>
                                    <Form.Group className="mb-3" controlId="formBasicCheckbox">
                                        <Form.Check type="checkbox" label="Recordar Usuario" className="small fw-light" />
                                    </Form.Group>
                                    <div className="d-grid gap-2">
                                        <Button variant="light" size="lg" onClick={logOn}>
                                            <IoEnterOutline /> <span className="small fw-light">Ingresar</span>
                                        </Button>
                                    </div>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                    <Row className="justify-content-center">
                        <Col xs={6}>
                            <p className="text-center">
                                <Link to="/soporte" className="link-dark text-decoration-none fw-light small">Recuperar Contraseña</Link>
                            </p>
                            <p className="text-center">
                                <IoHelpBuoyOutline /><br />
                                <Link to="/soporte" className="link-dark text-decoration-none fw-light">Soporte</Link>
                            </p>
                        </Col>
                    </Row>
                </Container>
            </LoginLayout>
        </>
    )
}

export default Login