import { Container, Row, Col, Form, FormControl, InputGroup, Button, Card, Pagination } from 'react-bootstrap'
import { IoBusinessOutline, IoLocationOutline, IoPersonSharp, IoPhonePortraitOutline, IoArrowUndoOutline } from "react-icons/io5";
import { Link } from 'react-router-dom';

import AppLayout from '../layout/AppLayout'
import DependenciasService from '../services/DependenciasService';

const Dependencias = () => {


    const dependencias = DependenciasService.findAll().list

    let items = []
    let active = 2

    for(let number=1; number < 5; number++){
        items.push(
            <Pagination.Item key={number} active={number === active}>
              {number}
            </Pagination.Item>,
          );
    }

    return(
        <>
            <AppLayout>
                <Container>
                    <Row className='mt-4 mb-2'>
                        <Col className='mb-4'>
                            <Link to='/' className='link-dark text-decoration-none'> <IoArrowUndoOutline /> Home</Link>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12}>
                            <Form>
                                <Form.Group>
                                    <Form.Label></Form.Label>
                                    <InputGroup className="mb-3">
                                        <FormControl
                                        placeholder="Dependencia..."
                                        aria-label="Recipient's username"
                                        aria-describedby="basic-addon2"
                                        />
                                        <Button variant="outline-secondary" id="button-addon2">
                                        Buscar
                                        </Button>
                                    </InputGroup>
                                </Form.Group>
                            </Form>
                        </Col>
                    </Row>
                    <Row className='mb-2'>
                        <Col xs={12}>
                            <h5 className='fw-light border-bottom border-gray pb-2'>
                                <IoBusinessOutline /> Dependencias
                            </h5>
                        </Col>
                    </Row>
                    <Row className='mb-4'>
                        <Col xs={12}>
                            {
                                dependencias.map((e, index) => {
                                    return(
                                        <Card key={index} className='border-light shadow-sm mb-3'>
                                            <Card.Body>
                                                <address>
                                                    <strong>{e.name}</strong> <br />
                                                    <IoLocationOutline /> <span className='fw-light'>{e.location}</span> <br />
                                                    <IoPhonePortraitOutline /> <span className='fw-light'>{e.phone}</span> <br />
                                                    <IoPersonSharp /> <span className='fw-light'>{e.responsable}</span> <br />
                                                </address>

                                            </Card.Body>
                                        </Card>
                                    )
                                })
                            }
                        </Col>
                    </Row>
                    <Pagination className='justify-content-center'>
                        {items}
                    </Pagination>
                </Container>
            </AppLayout>
        </>
    )
}

export default Dependencias